import * as React from "react"
import { useState, useEffect } from 'react';

import "./global.css"

import type { HeadFC, PageProps } from "gatsby"

import "bootstrap-honoka/dist/css/bootstrap.min.css"

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import Container from 'react-bootstrap/Container';

import About from "./(contents)/about";
import Date from "./(contents)/date";
import Group from "./(contents)/group"
import Past from "./(contents)/past"
import Twitter from "./(contents)/twitter"

import { Heading } from "../components/heading";

import { Header } from "../components/header";
import { Footer } from "../components/footer";

const base64FromTxt = async (path: string) => {
  const response = await fetch(path);
  const data = await response.text();
  return data;
}

const GradientBackground = ({ children }: { children: React.ReactNode }) => {
  const [hue, setHue] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setHue((prevHue) => (prevHue + 1) % 360);
    }, 300);

    return () => clearInterval(interval);
  }, [children]);

  const backgroundStyle = {
    background: `linear-gradient(90deg, hsl(${hue}, 100%, 90%) 0%, hsl(${(hue + 45) % 360}, 100%, 90%) 100%)`,
  };

  return (
    <div className="root" style={backgroundStyle}>
      {children}
    </div>
  )
}

const IndexPage: React.FC<PageProps> = () => {
  useEffect(() => {
    base64FromTxt("/omake/qr1.txt").then((data) => {
      console.log("%c ", `background: url(${data}); background-size: 100% 100%; padding: 150px 150px`);
    })
  }, []);
  useEffect(() => {
    base64FromTxt("/omake/qr2.txt").then((data) => {
      console.log("%c ", `background: url(${data}); background-size: 100% 100%; padding: 150px 150px`);
    })
  }, []);

  return (
    <GradientBackground>
      <Header />
      <Container>
        <div id="about" className="section">
          <Heading as="h1">概要</Heading>
          <About />
        </div>
        <div id="date" className="section">
          <Heading as="h1">開催内容</Heading>
          <Date />
        </div>
        <div id="group" className="section">
          <Heading as="h1">参加団体</Heading>
          <Group />
        </div>
        <div id="past" className="section">
          <Heading as="h1">過去の開催</Heading>
          <Past />
        </div>
        <div id="x-twitter" className="section">
          <Heading as="h1">𝕏(Twitter)</Heading>
          <Twitter />
        </div>
      </Container >
      <Footer />
    </GradientBackground>
  )
}

export default IndexPage

