import React, { useState } from 'react';

import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Container from 'react-bootstrap/Container';

import "./index.css";

export const Header = () => {
  const [expanded, setExpanded] = useState(false);

  const handleNavClose = () => setExpanded(false);
  return (
    <Navbar expand="lg" className="bg-body-tertiary navbar navbar-light header" fixed="top" bg="light" variant="light" expanded={expanded} onToggle={setExpanded}
    >
      <Container className="justify-content-between"
      >
        <Navbar.Brand href="">技術系合同新歓2024 特設サイト</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto" onClick={handleNavClose}
          >
            <Nav.Item className="nav-item">
              <Nav.Link href="#about">概要</Nav.Link>
            </Nav.Item>
            <Nav.Item className="nav-item">
              <Nav.Link href="#date">開催内容</Nav.Link>
            </Nav.Item>
            <Nav.Item className="nav-item">
              <Nav.Link href="#group">参加団体</Nav.Link>
            </Nav.Item>
            <Nav.Item className="nav-item">
              <Nav.Link href="#past">過去の開催</Nav.Link>
            </Nav.Item>
            <Nav.Item className="nav-item">
              <Nav.Link href="#x-twitter">𝕏(Twitter)</Nav.Link>
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Container >
    </Navbar >
  )
}
